html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
body,
a,
p,
li,
ul,
h1,
h2,
h3,
h4,
h5,
strong,
div,
img {
  font-size: 0.12rem;
  font-family: "微软雅黑", arial;
  margin: 0;
  padding: 0;
  border: none;
}
ul,
li {
  list-style: none;
}
input:focus,
select:focus,
textarea:focus {
  border: 0.01rem solid #66afe9 !important;
  box-shadow: 1px 1px 10px rgba(102, 175, 233, 0.8);
}
#app {
  height: 100%;
}
body {
  height: 100%;
}
html {
  height: 100%;
}
.wrap {
  overflow-x: hidden;
}
.nav {
  position: absolute;
  z-index: 99;
  top: 0.62rem;
  left: 0;
  width: 100%;
}
.nav a {
  display: block;
  height: 0.42rem;
  border-bottom: 0.01rem solid #de4a56;
  line-height: 0.42rem;
  padding-left: 0.2rem;
  color: #fff;
}
.nav .router-link-exact-active {
  color: #fff;
  background: #d71726;
}
.layout {
  width: 2.3rem;
  padding-top: 0.1rem;
  overflow: hidden;
}
.layout span {
  font-size: 0.14rem;
  display: block;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  line-height: 0.5rem;
}
.head {
  height: 0.72rem;
}
.head .logo {
  float: left;
  width: 74%;
  padding: 0.1rem;
  color: #5a5a5a;
}
.head .logo img {
  width: 0.63rem;
  height: 0.48rem;
  float: left;
  margin-right: 0.1rem;
}
.head .logo span {
  font-size: 0.18rem;
  float: left;
  width: 0.63rem;
  height: 0.38rem;
  display: block;
  padding-top: 0.1rem;
}
.head .logo strong {
  font-size: 0.18rem;
}
.head .logo p {
  font-weight: bold;
  color: #808080;
}
.head .icon {
  float: left;
  width: 0.5rem;
  height: 0.45rem;
  background: #f55e6a;
  border-radius: 0.04rem;
  margin-top: 0.1rem;
}
.head .icon svg {
  fill: #fff;
  margin: 0.03rem 0.06rem;
  width: 0.38rem;
  height: 0.38rem;
  cursor: pointer;
}
.vux-drawer .actives {
  -webkit-transform: translate3d(2.3rem, 0px, 0px) !important;
  transform: translate3d(2.3rem, 0px, 0px) !important;
}
.vux-drawer .activer {
  -webkit-transform: translate3d(-2.3rem, 0px, 0px) !important;
  transform: translate3d(-2.3rem, 0px, 0px) !important;
}
.vux-drawer > .vux-drawer-content {
  background: #f55e6a;
}
.weui-grids:before,
.weui-grid:before,
.weui-grid:after,
.weui-grids:after {
  border: 0px !important;
}
.title {
  text-align: center;
  margin: 0.2rem 0 0.2rem 0;
}
.title p {
  color: #f55e6a;
  font-size: 0.18rem;
  font-weight: bold;
  line-height: 0.2rem;
}
.title img {
  width: 100%;
}
.title a {
  font-size: 0.2rem;
  font-weight: bold;
  color: #282828;
}
.page {
  margin: 0.2rem 0 0.6rem;
  color: #5a5a5a;
  overflow: hidden;
  text-align: center;
  clear: both;
}
.page .num {
  border: 0.01rem solid #EEE;
  margin: 0 0.015rem;
  padding: 0 0.05rem;
}
.page a {
  color: #999;
}
.page .active {
  color: #fff;
  font-weight: bold;
  border: 0.01rem solid #000;
  background: #000;
}
.page a:hover {
  color: #f55e6a;
  border: 0.01rem solid #000;
  background-color: #000;
}
.page .prev {
  border: 0.01rem solid #EEE;
  color: #ddd;
}
/* 路由切换动画 */
.child-view {
  width: 100%;
  height: 100%;
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  height: 100%;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}



